
.index{
  position: absolute;
  width: 100%;
  z-index: 50;
 
  top: 0;
}
.header{
  height: 75px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-shadow:0px 2px 10px rgba(0,0,0,0.2);
background-color: rgba(0,0,0,1);
opacity: 1
}
.header div{
     

    width: 40%;
    height: 75px;
    /*margin-top: 10px;*/
   margin: 0px auto;
  
  color: white;
}
.header img {
  
    height: 50px;
    width: 50px;
    top:12px;
    position: absolute;
    display: inline-block;

   /* margin: 15px 5px;*/
}
.header-tabbar{
 display: -webkit-box;
 display: -ms-flexbox;
 display: flex;
}
.header-tabbar div{
    padding: 0px;
   
    position: relative;
   
    -webkit-box-flex:1;
   
        -ms-flex:1;
   
            flex:1;
}
.header-tabbar-icon{
   
    margin-left: 50%;
    left: -25px;
  
     padding: 0px;
    opacity: 0.5;
    display: inline-block;
}
.search-icon{
    right:10px;
}
.menu-icon{
    left: 10px;
}
.active img{
 font-weight: 900;
  opacity: 1
}
a, a:link,a:visited {
  outline: 0;
  text-decoration: none;
  color: black;
}





