
.circle-div p{
  position: absolute;
  display: block;
  z-index: 500;
  left: 50%;
  margin-left:-60px; 
  width: 120px;
  font-size: 25px;
  color: white;
}
.player{
  overflow: hidden;
  background-color: black;/*change*/
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 20;
}
.player-header{
  width: 100%;
  height: 75px;

  top: 0;

/*  box-shadow:0px 2px 10px rgba(0,0,0,0.3);*/
  position: relative;
  z-index: 20;
}
.player-header img {
  height: 50px;
  width: 50px;
  top:12px;
  
  position: absolute;
  display: inline-block;
}
.icon-back{
  left: 10px;
}
.icon-weather{
  right: 10px;
}
.player-header p{
  font-size: 35px;
  width: 79%;
  /*margin-top: 10px;*/
  margin: 20px auto;
  /* border: 1px solid;*/
  color: white;
}
.circle-div{
  height:460px;
  width:460px;
  position: absolute;
  top: 200px;
  left: 50%;
  margin-left: -230px;
}
.mask img{
  position:absolute;
  z-index:150;
  display: inline-block;
  top: 50%;
  left: 50%;
}
.icon-like{
  margin-top:-55px;
  margin-left: -55px;
  height:110px;
  width:110px;
}
.cd{
  height:100%;
  width:100%;
    margin-top:-50%;
  margin-left: -50%;
  border-radius: 50%;

    z-index:0;
  box-shadow: 0px 0px 100px rgba(0,0,0,1)
}
.up-cd{
  top: 130px;
  width: 300px;

  font-size: 30px;
    font-weight: bold;
    position: absolute;
}
.down-cd{

  top: 680px;
  left: 50%;
  margin-left: -180px;
  display: block;
  height: 600px;
}
.up-cd,.down-cd{
      position: absolute;
color: white;
   width:100%;
  
     margin-left: -50%;
  left: 50%;
}
/*.weather-city{
  top:15%;
}
.weather-content{
  top:65%;
}*/
.mask{
  position:absolute;  
  height:97%;
  width:97%;
  margin-left: -48.5%;
  margin-top: -48.5%;
  left: 50%;
  top: 50%;
  z-index:100;
}
#cir-right{
  position:absolute;
  z-index:30;
  background-color:red;
  clip:rect(0px,460px,460px,230px);
}
#cir-left{
  position:absolute;
  z-index:20;
  background-color:red;
  clip:rect(0px,230px,460px,0px);
}
.black{
  background-color: black;
}
.white{
  background-color: white;
}
/*.back2{
  background-image:url(http://pic13.huitu.com/res/20131012/369336_20131012183623803200_1.jpg) ;
}*/
#cir-back0{
  z-index:0;
  position:absolute;
}
#cir-back1{
  position:absolute;
  z-index:40;
  clip:rect(0px,230px,460px,0px);
background-color: black;
}
.cir{
  position:absolute;
  margin:auto;
  height:100%;
  width:100%;
  border-radius:50%;
}
.rotate{
  -webkit-transform:rotate(0deg);
          transform:rotate(0deg);
  -webkit-animation: circleProgressLoad_right 120s linear 1;
}
@-webkit-keyframes circleProgressLoad_right{
0%{-webkit-transform: rotate(0deg);
}
100%{-webkit-transform: rotate(360deg);
}
}
.button{
  position: absolute;
  top: 630px;
  left: 50%;
  margin-left: -180px;
  z-index: 1000;
}
.button img{
  height: 50px;
  margin: 27px;
}



