
.bg-weather{
  width: 100%;
  height: 75px;
  margin-left: 50%;
  position: absolute;
  left: -50%;
  opacity: 1;
}
.bg-cd{
  height: 100%;
  width: 200%;
  margin-left: -100%;
  position: absolute;
  opacity: 0.6;
}
.blur20 { 
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -ms-filter: blur(20px);    
  filter: blur(20px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=20, MakeShadow=false);
}
.blur5 { 
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -ms-filter: blur(5px);    
  filter: blur(5px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=5, MakeShadow=false);
}
.toPlayer-enter, .toPlayer-leave-active,.toPlayer-leave{
  -webkit-transform: translateX(600px);
          transform: translateX(600px);
}
.toSearch-enter, .toSearch-leave-active{
  opacity: 0;
}
#app {
  overflow: hidden;
  text-align: center;
  position: absolute;
  margin-top: 60px;
  width: 600px;
  height: 1000px;
  left: 50%;
  margin-left:-300px;
}




