
.search{
  position: absolute;
  height: 100%;
}
.search{
  height: 100%;
  width: 100%;
  
  z-index: 100;
  position: absolute;
}
/*.header{
  position: absolute;
  width: 100%;
}*/
.search-header {
    width: 100%;
    height: 75px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  
   background-color: black;
      box-shadow:0px 2px 10px rgba(0,0,0,0.);
position: absolute;
    z-index: 20;
}
.search-header input{
     
font-size: 30px;
    width: 440px;
    /*margin-top: 10px;*/
   margin: 15px 50px 15px 80px;
   border-radius: 10px;
   outline: none;
   /* border: 1px solid;*/
}
.search-icon, .back-icon{
 /* border: 1px solid;*/
    height: 50px;
    width: 50px;
    top:12px;
    position: absolute;
    display: inline-block;
   /* margin: 15px 5px;*/
}
.search-icon{
    right:10px;
}
.back-icon{
    left: 10px;
}
.cancel-icon{
  width: 30px;
  height: 30px;
  position: relative;
  right: 90px;
  top: 23px;
}
.search-tabbar{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  background-color: white;
  height: 55px;
  top: 75px;
    position: relative;
}
.search-result{

  position: relative;
height: 500px;
  top: 75px;
  background-color: white;
}
.result-title{
  text-align: left;
  font-size: 30px;
  font-weight: bold;
  padding: 10px 15px;
  color: black;
  background-color: #eee;
}
.result-song,.result-singer,.my-list{
margin-top: 30px;
border-bottom: 1px #eee solid;
position: relative;
text-align: left;
}
.result-normal{

 
  margin: 0px;
  padding: 0px 5px;
 
 width: 90%;
 height: 100%;
  display: inline-block;
  font-size: 25px;
}
.result-song img,.result-singersong img,.my-list img{
  width: 25px;
  height: 25px;
  position: absolute;
 right: 40px;
 top: 4px;
}
.search-tabbar div{
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    
    font-size: 18px;
  
    position: relative;
}
.search-tabbar-text{

    width: 100%;
    height: 100%;
position: absolute;
top: 0px;
left: 0px; 
padding: 10px;
font-size: 25px;
display: inline;
}
.active{
 font-weight: 900;
  border-bottom: 2px red solid;
}
a, a:link,a:visited {
  outline: 0;
  text-decoration: none;
  color: black;
}



