
.footer{
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 75px;
  background-color: white;
  box-shadow:0px -2px 10px rgba(0,0,0,0.3);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 600;
}
.img-div{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.img-div img{
  position: relative;
  width: 55px;
  height: 55px;
}
.text-div{
  -webkit-box-flex:5;
      -ms-flex:5;
          flex:5;
}
.text-div h3{
  top: -10px;
  position: absolute;
}
.text-div p{
  top:40px;
  position: absolute;
}
.show-div{
  -webkit-box-flex:2;
      -ms-flex:2;
          flex:2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.icon-div{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.icon-div img{
  height: 55px;
  top: 5px;
  position: relative;
  padding: 12px
}



