
.slider-button{
  position: relative;
  background-color: white;
  height: 15px;
  width: 15px;
  margin: 10px; 
  border-radius: 50%;
  top:-40px;

  display:inline-block;
}
.slider-enter-active,.slider-leave-active{
transition:all 0.6s linear;
}
.slider-enter{
 -webkit-transform: translateX(550px);
         transform: translateX(550px);
}
.slider-leave-active{
 -webkit-transform: translateX(-550px);
         transform: translateX(-550px);
}
.sliderlist{
  height: 280px;
}
.slider-img{
  position: absolute;
  height: 280px;
  width: 600px;
  margin-left: -300px;
  display: inline-block;
}
.slider-move{

  -webkit-animation-name: move;

          animation-name: move;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-timing-function:linear;
          animation-timing-function:linear;
  /*animation-iteration-count: infinite;*/
}
@-webkit-keyframes move{
0%   {margin-left: -100%;
}
10%  {margin-left: -200%;
}
20%  {margin-left: 0;
}
30%  {margin-left: -100%;
}
}
@keyframes move{
0%   {margin-left: -100%;
}
10%  {margin-left: -200%;
}
20%  {margin-left: 0;
}
30%  {margin-left: -100%;
}
}
.commend{
  

  position: absolute;
  height: 100%;
  width: 100%;
}
.radiolist{
  position: relative;

 width: 100%;
 display: -webkit-box;
 display: -ms-flexbox;
 display: flex;
}
.radiolist-div{

display: inline-block;

  height: 300px;
  position: relative;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.radiolist-div img{
display: inline-block;

 width: 90%;
  position: relative;
}
.commend-title{
  margin-left: 10px;
  text-align: left;
  font-size: 30px;
  font-weight: bold;
  padding: 20px 5px 5px 5px;
  color: black;
  border-bottom: 1px #eee solid;
}



